
html, body, widow {
  padding: 0;
  margin: 0;
}
body{
  background: rgb(32, 160, 255);
}
.elm {
  padding: 0;
  height: 100vh;
  width: 100%;
  -webkit-transition: background-color 1s ease;
  transition:background-color 1s ease; 
  position: relative;
}
#direction{
  padding: 10px;
  height: 50px;
  width: 50px;
  background: rgb(32, 160, 255);
  font-size: 20pt;
  color: #fff;
  border-radius: 60%;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: 2px solid #fff;
  text-align: center;
}



